.App {
  text-align: center;
}

.App-header {
  background-color: #5677b9;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: hsl(0, 0%, 100%);
}

body{
  background-color: #ffffff;
}

section {
  display: grid;
  place-items: center;
  height: 100vh;
  font-size: 32px;
  
}

h5 {
  font-weight: bold
}

.nav {
  font-family: sans-serif;
	display: flex;
	align-items: center;
}


a {
  text-decoration: none; 
  color: rgb(255, 255, 255); 
}


ul {
  display: flex;
  gap: 1rem;
  font-size: 0.9rem;
  list-style: none;
  padding: 0;
}

li {
  cursor: pointer;
}

li:hover {
  text-decoration: underline;
}